$borderWidth:3px;

.loadingRing {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
  border: $borderWidth solid #ccc;
  border-radius: 50%;
  div {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left:-$borderWidth;
    top:-$borderWidth;
    border: $borderWidth solid #2DBD60;
    border-radius: 50%;
    animation: loadingRing 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #2DBD60 transparent transparent transparent;
    &:nth-child(1){
      animation-delay: -0.45s;
    }
    &:nth-child(2){
      animation-delay: -0.3s;
    }
    &:nth-child(3){
      animation-delay: -0.15s;
    }
  }
}
@keyframes loadingRing {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
